*{

  font-family: Helvetica,Arial,Sans-serif;
}

body{
  padding: 20px;
}

input{

  color: grey;
  font-size: 1.2rem;
  width: 100%;
  border: 1px solid green;
}

.card-grid{

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{

  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 5px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 180px;
}